<template>
  <div  
        :class="warning && showWarning ? 'warning' : ''"
        class="date">
        <span class="date-day">{{ day }}</span>
      <span class="date-month">{{ month | monthName }}</span>
      <span class="date-year">{{ year }}</span>
  </div>
</template>

<script>
import moment from 'moment';

export default {
    props: ['date', 'showWarning'],
    data(){
        return {
            day: '',
            month: '',
            year: '',
            warning: false
        }
    },
    filters: {
        monthName(value){
            switch (value) {
                case "01":
                    return "JAN"
                    break;
                case "02":
                    return "FEV"
                    break;
                case "03":
                    return "MAR"
                    break;
                case "04":
                    return "ABR"
                    break;
                case "05":
                    return "MAI"
                    break;
                case "06":
                    return "JUN"
                    break;
                case "07":
                    return "JUL"
                    break;
                case "08":
                    return "AGO"
                    break;
                case "09":
                    return "SET"
                    break;
                case "10":
                    return "OUT"
                    break;
                case "11":
                    return "NOV"
                    break;
                case "12":
                    return "DEZ"
                    break;
            }
        }
    },
    mounted(){
        const arr = this.date.split("-");
        this.day = arr[2];
        this.month = arr[1];
        this.year = arr[0];
        const today = moment(new Date());
        const duoDate = moment(this.date);
        this.warning = duoDate.diff(today, 'days') < 5;
    }
}
</script>

<style lang="sass" scoped>
.date
    font-size: 12px
    span
        display: block
        text-align: center
    &-day,
    &-month
        font-weight: bold
    &-day,
    &-year
        line-height: 10px
    &-day
        font-size: 16px
</style>