<template>
  <div>
    <div class="dashboard">
      <loader v-if="isLoading"/>
      <div class="container-fluid" v-if="!isLoading">
        <div class="card" v-if="!isLoading">
          <h3 class="header-amounts-title">Saldo de Recorrentes / Parcelados do mês</h3>
          <strong class="header-amounts-value">
              R$ {{ dashboard.total_recurrent_parceled_amount | currency }}
          </strong>
        </div>
        <div class="row">
          <receivable-accounts 
            :items="dashboard.receivable_accounts"
            :total="dashboard.total_receivable_accounts" />
          <payable-accounts 
            :items="dashboard.payable_accounts"
            :total="dashboard.total_payable_accounts" />
          <financial-accounts 
            :items="dashboard.financial_transactions" 
            :total="dashboard.total_deposit_account"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PayableAccounts from '../components/Dashboard/PayableAccounts.vue';
import FinancialAccounts from '../components/Dashboard/FinancialAccounts.vue';
import ReceivableAccounts from '../components/Dashboard/ReceivableAccounts.vue';
import { bus } from '@/main';

export default {
  components: {
    PayableAccounts,
    FinancialAccounts,
    ReceivableAccounts
  },
  name: 'dashboard',
  data(){
    return{
      isLoading: true
    }
  },
  computed: {
    dashboard(){
      return this.$store.state.dashboard
    }
  },
  created() {
    bus.$on('loadingDashboard', (data) => {
      this.isLoading = data;
    });

  }
};
</script>
