<template>
    <header 
        v-if="!isLoading"
        :class="dashboard.total_month_amount > 0 ? 'bg-positive' : 'bg-negative'"
        class="header">
        <div class="breadcrumb">
            <h2 class="breadcrumb-title">Dashboard</h2>
        </div>
        <v-select 
            :options="months" 
            :reduce="month => month.code"
            v-model="currentMonth" 
            @input="selectMonth">
        </v-select>
        <div class="header-amounts">
            <h3 class="header-amounts-title">Saldo do mês</h3>
            <strong class="header-amounts-value">
                R$ {{ dashboard.total_month_amount | currency }}
            </strong>
        </div>
        <a href="#" class="header-logout" @click.stop.prevent="logout">Sair</a>
    </header>
</template>

<script>

import { bus } from '@/main.js';
import firebase from 'firebase/app';
import 'firebase/auth';
import 'firebase/firestore';

export default {
    data(){
        return {
            currentMonth: null,
            months: [
                {
                    code: 1,
                    label: "Janeiro"
                },
                {
                    code: 2,
                    label: "Fevereiro"
                },
                {
                    code: 3,
                    label: "Março"
                },
                {
                    code: 4,
                    label: "Abril"
                },
                {
                    code: 5,
                    label: "Maio"
                },
                {
                    code: 6,
                    label: "Junho"
                },
                {
                    code: 7,
                    label: "Julho"
                },
                {
                    code: 8,
                    label: "Agosto"
                },
                {
                    code: 9,
                    label: "Setembro"
                },
                {
                    code: 10,
                    label: "Outubro"
                },
                {
                    code: 11,
                    label: "Novembro"
                },
                {
                    code: 12,
                    label: "Dezembro"
                }
            ],
            isLoading: true
        }
    },
    computed: {
        dashboard() {
            return this.$store.state.dashboard
        }
    },
    created() {
        const date = new Date();
        this.currentMonth = (date.getMonth()) + 1;
        this.$store.dispatch('fetch_dashboard', {app: this});
        this.$store.commit('GET_CURRENT_MONTH', this.currentMonth);
        bus.$on('loadingDashboard', (data) => {
            this.isLoading = data;
        });
    },
    methods: {
        selectMonth() {
            this.$store.commit('GET_CURRENT_MONTH', this.currentMonth);
            this.$store.dispatch('fetch_dashboard', {app: this});
        },
        logout() {
            firebase.auth().signOut().then(() => {
                this.$router.replace('login');
            })
        }
    }
}
</script>
