<template>
    <span class="input-group">
        <input 
            class="input"
            :class="name ? 'filled' : ''" 
            :value="name" 
            @input="updateSelf($event.target.value)"
            :type="type" 
            :id="idFor" />
        <label class="label" :for="idFor">
            {{ label }}
        </label>
    </span>
</template>

<script>
export default {
    model: {
        prop: "name",
        event: "input"
    },
    props: ["name", "idFor", "type", "label"],
    data(){
        return{
            value: this.name
        }
    },
    methods: {
        updateSelf(name) {
            this.$emit("input", name);
        }
    }
}
</script>

