<template>
    <div class="col payable-accounts">
        <div class="row">
            <div class="col-md-6">
                <h2>
                    Contas a Pagar {{ currentDay }}
                </h2>
            </div>
            <div class="col-md-6 text-right">
                <h2 class="negative">R$ {{ total | currency }}</h2>
            </div>
        </div>
        <ul class="card-list" v-if="!showCreditCardInvoice">
            <perfect-scrollbar>
                <li 
                    class="card-list-item"
                    :class="conta.category_id == 3661584 ? 'clickabled' : ''"
                    v-for="(conta, index) in items" 
                    :key="index" 
                    v-on="conta.category_id == 3661584 ? { click: () => getCreditCardInvoices(conta) } : {}">
                    <div class="card-list-item-date">
                        <date
                            :show-warning="true" 
                            :date="conta.due_date" />
                    </div>
                    <div class="card-list-item-description">
                        {{ conta.description }}
                    </div>
                    <div class="card-list-item-amount">
                        <b>{{ conta.amount | currency }}</b>
                    </div>
                </li>
            </perfect-scrollbar>
        </ul>
        <ul class="card-list" v-if="showCreditCardInvoice">
            <perfect-scrollbar>
                <div class="card-list-item" v-if="creditCardInvoice">
                    <a href="#" class="card-list-item-link-back" @click="backPayableAccounts">
                        <img src="@/assets/img/voltar.svg" />
                    </a>
                    <div class="card-list-item-description">
                        {{ creditCardInvoice.description }}
                    </div>
                    <div class="card-list-item-amount">
                        <b>{{ creditCardInvoice.amount | currency }}</b>
                    </div>
                </div>
                <li 
                    class="card-list-item"
                    v-for="(conta, index) in creditCardtransactions" 
                    :key="index" >
                    <div class="card-list-item-date">
                        <date 
                            :show-warning="false"
                            :date="conta.credit_card_invoice.due_date" />
                    </div>
                    <div class="card-list-item-description">
                        {{ conta.description }}
                        <span v-if="conta.parcels">
                            &nbsp;-&nbsp; {{ conta.parcel }}/{{ conta.parcels }}
                        </span>
                    </div>
                    <div class="card-list-item-amount">
                        <b>{{ conta.amount | currency }}</b>
                    </div>
                </li>
            </perfect-scrollbar>
        </ul>
        <app-modal v-if="showModal">
            <div class="container">
                <div class="col-md-12">
                    <app-input idFor="description" label="Descrição" type="text" v-model="payable_account.description"></app-input>
                    <app-input idFor="amount" label="Valor" type="text" v-model="payable_account.amount"></app-input>
                    <app-input idFor="due_date" label="Vencimento" type="text" v-model="payable_account.due_date"></app-input>
                    <app-button label="Salvar" @click="savePayableAccount" />
                </div>
            </div>
        </app-modal>
    </div>
</template>

<script>

import { bus } from '@/main.js';

export default {
    name: "payable-accounts",
    props: ['items', 'total'],
    data(){
        return {
            creditCardInvoice: '',
            showCreditCardInvoice: false,
            currentDay: null,
            showModal: false,
            form: {
                description: null,
                amount: null,
                due_date: null
            }
        }
    },
    computed: {
        currentMonth(){
            return this.$store.state.dashboard.current_month
        },
        creditCardtransactions(){
            return this.$store.state.dashboard.credit_card_transactions
        },
        payable_account(){
            return this.$store.state.dashboard.payable_account
        }
    },
    methods: {
        addPayableAccount(){
            this.showModal = !this.showModal;
        },
        savePayableAccount(){
            this.$store.dispatch('fetch_dashboard_new_payable_account', this.payable_account);
        },
        backPayableAccounts(){
            this.showCreditCardInvoice = false;
        },
        getCreditCardInvoices(conta){
            this.creditCardInvoice = conta;
            const credit_card_name = conta.description.split(' ')[5];
            const isCreditCard = (el) => { return el.credit_card.name == credit_card_name };
            const credit_card = this.$store.state.dashboard.credit_cards.filter(isCreditCard);
            this.$store.dispatch('fetch_dashboard_credit_card_transactions', [credit_card[0].credit_card.id, this.currentMonth, { app: this }]);
            this.showCreditCardInvoice = true;
        }
    }
};
</script>
