import filter from '@/mixins/filter';
import { converNumberTwoLength } from '@/mixins/functions';

const mutations = {
    GET_CURRENT_MONTH(state, currentMonth) {
        state.current_month = converNumberTwoLength(currentMonth);
    },
    GET_DASHBOARD(state, [payloads, {app}]) {
        
        let receivable_accounts = new Array()
        let payable_accounts = new Array()
        payloads.receivable_accounts.map((el) => {
            receivable_accounts.push(el.receivable_account)
        })
        
        payloads.payable_accounts.map((el) =>{
            payable_accounts.push(el.payable_account)
        })
        
        payloads.receivable_accounts = receivable_accounts.filter(filter.byMonthInReceivable(app.currentMonth));
        payloads.payable_accounts = payable_accounts.filter(filter.byMonthInPayable(app.currentMonth));
        
        Object.assign(state, payloads);

    },
    GET_TOTAL_AMOUNT(state) {
        state.total_payable_accounts = 0;
        state.total_receivable_accounts = 0;
        state.total_deposit_account = 0;
        state.total_month_amount = 0;
        state.total_recurrent_parceled_amount = 0;
        
        const payableAccountLength = state.payable_accounts.length;
        for (let i = 0; i < payableAccountLength; i++) {
            state.total_payable_accounts += -state.payable_accounts[i].amount;
            if(state.payable_accounts[i].recurrent && 
                +state.payable_accounts[i].due_date.split("-")[1] == state.current_month){
                state.total_recurrent_parceled_amount += -state.payable_accounts[i].amount;
            }
        }
        
        const receivaableAccountLength = state.receivable_accounts.length;
        for(let i = 0; i < receivaableAccountLength; i++){
            state.total_receivable_accounts += +state.receivable_accounts[i].amount;
        }

        state.total_deposit_account = +state.deposit_accounts.deposit_account.calculated_balance;

        state.total_month_amount = state.total_payable_accounts + state.total_receivable_accounts + state.total_deposit_account
    },

    GET_DASHBOARD_CREDIT_CARD_TRANSACTIONS(state, payload){

        state.credit_card_transactions = new Array()
        payload.map((el) => {
            state.credit_card_transactions.push(el.credit_card_transaction)
        })

    }
}

export default mutations;