import axios from 'axios';
import { $http, options } from '../../plugins/axios';
import { bus } from '@/main';

const actions = {
    fetch_dashboard({commit}, {app}){
        bus.$emit('loadingDashboard', true);
        function getReceivableAccounts(){
            return $http('/receivable_accounts?per_page=15&page=1')
        }
        function getPayableAccounts(){
            return $http('/payable_accounts?per_page=15&page=1')
        }
        function getFinancialTransactions(){
            return $http('/deposit_accounts/95233/financial_transactions?per_page=15&page=1')
        }
        function getDepositAccounts(){
            return $http('/deposit_accounts/95233')
        }
        function getCreditCards(){
            return $http('/credit_cards')
        }
        axios.all([
            getReceivableAccounts(), 
            getPayableAccounts(),
            getFinancialTransactions(),
            getDepositAccounts(),
            getCreditCards(),
         ]).then(axios.spread((
             receivable_accounts, 
             payable_accounts,
             financial_transactions,
             deposit_accounts,
             credit_cards) => {
            const payloads = {
                receivable_accounts: receivable_accounts.data,
                payable_accounts: payable_accounts.data,
                financial_transactions: financial_transactions.data,
                deposit_accounts: deposit_accounts.data,
                credit_cards: credit_cards.data
            }
            commit('GET_DASHBOARD', [payloads, {app}]);
            commit('GET_TOTAL_AMOUNT');
            bus.$emit('loadingDashboard', false);
        }))
        .catch((err) => {
            console.log('Erro:', err);
            bus.$emit('loadingDashboard', false);
        })
    },

    fetch_dashboard_credit_card_transactions({ commit }, [id, currentMonth]){
        $http(`/credit_cards/${id}/invoices/2020-${currentMonth}?per_page=80&page=1`)
        .then(res => {
            commit('GET_DASHBOARD_CREDIT_CARD_TRANSACTIONS', res.data);
        })
    },

    fetch_dashboard_new_payable_account({ commit }, form){
        const data = new Object();
        data.payable_account = form;
        $http.post('/payable_accounts', data)
        .then(res => {
            alert('deu bom');
            commit('ADD_DASHBOARD_PAYABLE_ACCOUNT', res.data);
        })
        .catch(err => {
            alert('deu ruim');
            console.log(err);
        })
    }

}

export default actions;