<template>
    <div class="container login">
        <div class="row">
            <div class="col-md-4 offset-md-4">
                <form class="card" autocomplete="off">
                    <app-input idFor="email" label="Email" type="text" v-model="email"></app-input>
                    <app-input idFor="password" label="Senha" type="password" v-model="password"></app-input>
                    <app-button label="Entrar" :disabled="!(email && password)" @click="sendUser" />
                </form>
            </div>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase/app';
import 'firebase/auth';
import 'firebase/firestore';

export default {
    name: 'login',
    data(){
        return {
            email: '',
            password: ''
        }
    },
    methods: {
        sendUser(){
            firebase.auth().signInWithEmailAndPassword(this.email, this.password)
            .then((res) => {
                this.$router.replace('dashboard').catch(err => {})
            })
            .catch((err) => {
                alert('deu ruim');
            })
        }
    }
}
</script>
