const filter = {
    byMonthInReceivable: function(month) {
        return function(account) {
            return parseInt(account.due_date.split("-")[1]) <= month;
        }
    },
    byMonthInPayable: function(month) {
        return function(account) {
            return parseInt(account.due_date.split("-")[1]) <= month;
        }
    }
}

export default filter;