<template>
  <button 
    class="button"
    @click.stop.prevent="handleClick">
    <span>
        {{ label }}
    </span>
</button>
</template>

<script>
export default {
    props: {
        label: {
            required: true,
            type: String
        }
    },
    methods: {
        handleClick() {
    	    this.$emit('click')
        }
    }

}
</script>
